.content__description {
  margin: 9vh 18vw 9vh 18vw;
  text-align: center;
  font-size: 1.2rem; }

.content__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 7vh;
  background-color: #BD9593; }
  .content__link--wright {
    color: white;
    font-size: 1.6rem;
    text-decoration: none; }

.content__title {
  display: flex;
  align-items: center;
  color: #AB5D55;
  background-color: #f9f9f9;
  width: 100%;
  height: 14vh;
  margin: 9vh 0 9vh 0;
  font-weight: 300;
  font-size: 2rem;
  padding-left: 20vw; }

.content__gallery {
  display: grid;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr);
  margin-bottom: 9vh; }
  .content__gallery--photo {
    display: flex;
    justify-self: center;
    align-self: center;
    max-width: 30vw;
    height: auto; }

.content__instagram {
  width: 100%;
  margin: 9vh 0 9vh 0;
  font-weight: 300;
  font-size: 2rem; }
  .content__instagram--logo {
    display: flex;
    align-items: center;
    color: #AB5D55;
    text-decoration: none;
    text-align: center;
    padding-left: 10vw; }
    .content__instagram--logo:before {
      content: "";
      background: url("../assets/img/insta_icon60x60.svg") no-repeat;
      width: 60px;
      height: 60px;
      margin-right: 2vw; }

.header {
  width: 100%;
  height: 88vh;
  margin-top: 12vh;
  background: url("../assets/img/header.JPG") no-repeat center;
  background-size: cover; }
  .header__title {
    color: #AB5D55;
    text-align: center;
    font-weight: 300;
    font-size: 2.6rem;
    margin-left: 45%;
    padding-top: 7%; }

.instagram__gallery {
  display: grid;
  grid-column-gap: 10px;
  grid-row-gap: 10px;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(1, 1fr);
  margin-bottom: 9vh;
  margin-top: 9vh; }
  .instagram__gallery--photo {
    display: flex;
    justify-self: center;
    align-self: center;
    max-width: 30vw;
    height: auto; }

.nav {
  width: 100%;
  position: fixed;
  top: 0; }
  .nav__menu {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    margin: 0;
    padding-left: 9vw;
    padding-right: 9vw;
    background-color: white; }
  .nav__logo {
    height: 30vh;
    width: auto;
    margin-top: -9vh;
    margin-bottom: -9vh; }
  .nav__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 8vw;
    height: 4vh;
    font-family: "Montserrat", sans-serif;
    font-size: 1.1rem;
    text-decoration: none;
    color: #ab5d55; }
  .nav__button {
    transition: all 0.3s;
    list-style-type: none;
    border: 2px solid rgba(193, 166, 181, 0.9); }
    .nav__button:hover {
      transition: all 0.3s;
      border: 2px solid rgba(107, 73, 100, 0.9); }
    .nav__button--active {
      border: 2px solid rgba(107, 73, 100, 0.9); }

.contact__title {
  display: flex;
  align-items: center;
  color: #AB5D55;
  background-color: #f9f9f9;
  width: 100%;
  height: 14vh;
  margin: 9vh 0 9vh 0;
  font-weight: 300;
  font-size: 2rem;
  padding-left: 20vw; }

.contact__container {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(3, 1fr);
  margin: 0 0 5vw 18vw; }

.contact__email {
  grid-area: 1 / 1 / 2 / 2; }
  .contact__email--link {
    text-decoration: none;
    color: #AB5D55;
    font-size: 1.2rem; }

.contact__telephone {
  grid-area: 2 / 1 / 3 / 2; }
  .contact__telephone--color {
    color: #AB5D55;
    font-size: 1.2rem; }

.contact__instagram {
  display: flex;
  align-items: center;
  grid-area: 1 / 2 / 2 / 3; }
  .contact__instagram--link {
    color: #AB5D55;
    text-decoration: none;
    font-size: 1.2rem;
    height: 30px;
    background: url("../assets/img/inst_icon30x30.svg") no-repeat; }
    .contact__instagram--link:before {
      content: "";
      width: 30px;
      height: 30px;
      margin-right: 3vw; }

.contact__facebook {
  display: flex;
  align-items: center;
  grid-area: 2 / 2 / 3 / 3; }
  .contact__facebook--link {
    color: #AB5D55;
    text-decoration: none;
    font-size: 1.2rem;
    height: 30px;
    background: url("../assets/img/fb_icon30x30.svg") no-repeat; }
    .contact__facebook--link:before {
      content: "";
      width: 30px;
      height: 30px;
      margin-right: 3vw; }

.contact__pinterest {
  display: flex;
  align-items: center;
  grid-area: 3 / 2 / 4 / 3; }
  .contact__pinterest--link {
    color: #AB5D55;
    text-decoration: none;
    font-size: 1.2rem;
    height: 30px;
    background: url("../assets/img/pinterest_icon30x30.svg") no-repeat; }
    .contact__pinterest--link:before {
      content: "";
      width: 30px;
      height: 30px;
      margin-right: 3vw; }

.footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #f9f9f9;
  width: 100%;
  height: 70px; }
  .footer__description {
    font-size: 1.2rem; }

html {
  box-sizing: border-box;
  margin-top: 0; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  color: #585858;
  font-family: 'Cormorant', serif;
  letter-spacing: 1px; }
